<template>
<infinite-scroll class="content topic" ref="infiniteScroll"
    @refresh="refreshAction" @loadmore="loadmoreAction">
        <!--  -->
        <div v-for="(item, index) in list" :key="item.id">
            <component :is="`item-type${item.type}`" :data="item" :index="index"/>
        </div>
</infinite-scroll>

</template>

<script>
import {mapState} from 'vuex'
import TopicItem0 from './topic-item-type0'
import TopicItem1 from './topic-item-type1'
import TopicItem7 from './topic-item-type7'
import TopicItem8 from './topic-item-type8'
export default {
    name: 'topic-content',
    components: {
        [TopicItem0.name]: TopicItem0,
        [TopicItem1.name]: TopicItem1,
        [TopicItem7.name]: TopicItem7,
        [TopicItem8.name]: TopicItem8
    },
    props: {
        id: [String, Number]
    },
    data(){
        return {
            size: 20,
            page: 1
        }
    },
    computed: {
        ...mapState({
            list: state=>state.topic.topicData.list
        })
    },
    watch: {
        id: {
            handler(){
                this.page = 1;
                this.$refs.infiniteScroll && this.$refs.infiniteScroll.scroll.scrollTo(0, -40);
                // 
                this.$store.dispatch('topic/getTopicListAction', 
                    {id: this.id, size: this.size, page: this.page, type: 'init'});
            },
            immediate: true
        },
        list(newVal, oldVal){
            // console.log(newVal);
            // console.log(oldVal);
            if((oldVal.length > 0) && (newVal.length > oldVal.length)){
                // 
                this.$refs.infiniteScroll.endLoadMore();
            }
            if(newVal.length <= oldVal.length){
                //  
                this.$refs.infiniteScroll.endRefresh();
            }
        }
    },
    methods: {
        // 
        refreshAction(){
            this.page = 1;
            this.$store.dispatch('topic/getTopicListAction', 
                {id: this.id, size: this.size, page: this.page, type: 'refresh'});
        },
        // 
        loadmoreAction(){
            this.page += 1;
            this.$store.dispatch('topic/getTopicListAction', 
                    {id: this.id, size: this.size, page: this.page, type: 'loadmore'});
        }
    },
    created(){
        /*
          
        params: tabId(:4,13,6) size(10) page(1)
        
        
        
        */
    }

}
</script>

<style lang="scss" scoped>
.topic{
   background: #efefef;
   .item{
        margin-top: 10px;
        background: #fff;
        padding: 10px;
   }
}
</style>

