<template>
<div class="preview">
    <van-icon name="cross" class="close" @click="closeAction"></van-icon>

    <p>{{currentIndex}}/{{goodsList.length}}</p>

    <div class="swiper-container list" ref="swiper">
        <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="item in goodsList"
                :key="item.itemId">
                <img :src="item.itemUrl"/>
            </div>
        </div>
    </div>

    <button @click="goDetail"></button>

</div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name: 'preview-goods',
    data(){
        return {
            currentIndex: null
        }
    },
    computed: {
        ...mapState({
            //
            imgIndex: state=>state.topic.previewInfo.imgIndex,
            // 
            listIndex: state=>state.topic.previewInfo.listIndex
        }),
        // 
        goodsList(){
            let state = this.$store.state;
            let list = state.topic.topicData.list;
            let imgList = list[this.listIndex].itemList;
            console.log(imgList);
            return imgList;
        }
    },
    methods: {
        // 
        closeAction(){
            this.$store.commit('topic/setShowPreview', {show: false});
        },
        // 
        goDetail(){
            let currentGoods = this.goodsList[this.currentIndex - 1];
            console.log(currentGoods);
            this.$router.push(`/topic/detail/${currentGoods.itemId}`);
        }
    },
    mounted(){
        // 
        this.currentIndex = this.imgIndex + 1;
        // 
        new Swiper(this.$refs.swiper, {
            initialSlide: this.imgIndex,
            onSlideChangeEnd: (swiper)=>{
                // 
                this.currentIndex = swiper.activeIndex + 1;
                
            }
        });
    }
}
</script>

<style lang="scss" scoped>
.preview{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.4);
    z-index: 5;
    .close{
        width: 60px;
        height: 60px;
        position: absolute;
        top: 0;
        right: 0;
        text-align: center;
        line-height: 60px;
        font-size: 30px;
        color: #fff;
    }
    .list{
        background: #fff;
        position: absolute;
        width: 100%;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        img{
            width: 100%;
        }
    }
}
</style>
