<template>
<div>
    <div class="page" id="topic">
        <app-header title="" :hasBackBtn="false"></app-header>
        <nav class="nav border-bottom">
            <li class="nav-item" v-for="(item, index) in navList" :key="item.tabId"
                :class="{active: index===selectIndex}"
                @click="selectNavTabAction(index)">
                {{item.tabName}}
            </li>
        </nav>
        
        <keep-alive>
            <top-content v-if="selectId==9"></top-content>
            <show-order-content v-else-if="selectId==7"></show-order-content>
            <topic-content v-else  :id="selectId"></topic-content>
        </keep-alive>

        <preview-goods v-if="showPreview"></preview-goods>
    </div>
    <transition enter-active-class="slideInRight" leave-active-class="slideOutRight">  
        <router-view></router-view>
    </transition>
</div>
</template>

<script>
import TopContent from './children/top-content'
import TopicContent from './children/topic-content'
import ShowOrderContent from './children/show-order-content'
import PreviewGoods from './children/preview-goods'
import {mapState} from 'vuex'
export default {
    components: {
        [TopContent.name]: TopContent,
        [TopicContent.name]: TopicContent,
        [ShowOrderContent.name]: ShowOrderContent,
        [PreviewGoods.name]: PreviewGoods
    },
    data(){
        return {
            navList: [
                {tabId: 9, tabName: ""},
                {tabId: 7, tabName: ""},
                {tabId: 4, tabName: ""},
                {tabId: 13, tabName: ""},
                {tabId: 6, tabName: "HOME"}
            ],
            selectIndex: 2
        }
    },
    computed: {
        ...mapState({
            showPreview: state=>state.topic.showPreview
        }),
        selectId(){
            return this.navList[this.selectIndex].tabId;
        }
    },
    methods: {
        selectNavTabAction(index){
            this.selectIndex = index;
        }
    }
}
</script>

<style lang="scss" scoped>
#topic{
    .nav{
        position: absolute;
        top: 44px;
        left: 0;
        width: 100%;
        height: 34px;
        display: flex;
        .nav-item{
            flex: 1;
            text-align: center;
            line-height: 32px;
            border-bottom: 2px solid transparent;
            font-size: 12px;
            color: #999;
            &.active{
                color: #b4282d;
                border-bottom: 2px solid #b4282d;
            }
        }
    }
    .content{
        position: absolute;
        top: 78px;
        bottom: 0;
        left: 0;
        width: 100%;

    }
}
</style>

